import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IStockHistory, IStockHistoryReq} from "../models/stockhistory.model";
import {InventoryEndpoints} from "../../inventory.endpoints";

@Injectable({
  providedIn: 'root'
})
export class StockHistoryService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.STOCK_HISTORIES.SEARCH(endpoint, params)
    this.http.get<ResponseData<IStockHistory>>(url)
      .subscribe((stockHistory: ResponseData<IStockHistory>) => {
        this.eventBusService.publish(key, stockHistory.body)
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<IStockHistory>(InventoryEndpoints.STOCK_HISTORIES.GET(id))
      .subscribe((stockHistory: IStockHistory) => {
        this.eventBusService.publish(key, stockHistory)
      }, (error: HttpErrorResponse) => {
         this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, stockHistoryReq: IStockHistoryReq, exId: number | null) {
    let endpoint = exId == null ?InventoryEndpoints.STOCK_HISTORIES.CREATE() :InventoryEndpoints.STOCK_HISTORIES.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, stockHistoryReq)
      : this.http.patch(endpoint, stockHistoryReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, stockHistoryReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
