import {Component, EventEmitter, Inject, LOCALE_ID} from '@angular/core';
import {IStockHistory, IStockHistoryDetail, resolveProductName} from "../../models/stockhistory.model";
import {StockHistoryConfig, defaultStockHistoryConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {BaseListComponentV3} from "../../../../../../../base/components/base-list-v3.component";
import {
  BasicFilterData, defaultFilterData,
  parseFilterData, toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {StockHistoryService} from "../../services/stockhistory.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {ComponentType} from "@angular/cdk/overlay";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {BaseDialogComponent} from "../../../../../../../base/components/base-dialog.component";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";

@Component({
  selector: 'app-stockhistory-list',
  templateUrl: './stockhistory-list.component.html',
  styleUrls: ['./stockhistory-list.component.scss']
})
export class StockHistoryListComponent extends BaseListComponentV3<IStockHistory, IStockHistoryDetail> implements BaseDialogComponent<IStockHistory> {

  config: StockHistoryConfig = defaultStockHistoryConfig
  filterData: BasicFilterData = defaultFilterData

  stockHistories: IPage<IStockHistory> = defaultPage

  onCanceled: EventEmitter<void> = new EventEmitter<void>();
  onCompleted: EventEmitter<IStockHistory | null> = new EventEmitter<IStockHistory | null>();

  stockId: number | null = null

  isWithinDialog: boolean = false;

  protected componentName(): string {
    return StockHistoryListComponent.name;
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IStockHistoryDetail>,
              private activatedRoute: ActivatedRoute,
              private stockHistoryService: StockHistoryService,
              private notifier: NotifierService,
              @Inject(LOCALE_ID) protected locale: string,
              @Inject(MAT_DIALOG_DATA) dialogData: any,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = selfEndpoints.adminEndpoint
    }
    if (dialogData != null) {
      this.stockId = dialogData.stockId
      this.isWithinDialog = dialogData.isWithinDialog
    }
  }

  initialize(): void {
    if (this.isWithinDialog) {
      this.fetchStockHistories({
        stock_id: this.stockId,
        page: 0,
      })
    } else {
      this.activatedRoute.queryParams.subscribe(params => {
        this.filterData = parseFilterData(params)
        this.fetchStockHistories({
          q: this.filterData.query,
          username: this.filterData.username,
          from_date: this.filterData.fromDate,
          to_date: this.filterData.toDate,
          page: this.filterData.page,
          size: this.filterData.size,
          stock_id: this.stockId
        })
      });
    }
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Stock Histories",
        link: null,
        active: true
      }
    ]
  }

  private fetchStockHistories(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STOCK_HISTORIES.SEARCH(params))
    this.stockHistoryService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.stockHistories = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "StockHistory is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IStockHistoryDetail>> | null {
    return null;
  }


  protected readonly resolveProductName = resolveProductName;

  onCloseClick() {
    this.onCanceled.emit()
  }

  onPreviousClick() {
    this.filterData.page = this.filterData.page - 1
    this.fetchStockHistories({
      stock_id: this.stockId,
      page: this.filterData.page
    })
  }

  onNextClick() {
    this.filterData.page = this.filterData.page + 1
    this.fetchStockHistories({
      stock_id: this.stockId,
      page: this.filterData.page
    })
  }
}
