import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";


export interface AttributeConfig extends BaseConfig {
}

export let defaultAttributeConfig: AttributeConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: InventoryEndpoints.ATTRIBUTES.ATTRIBUTE_ENDPOINT,
  delEndpoint: InventoryEndpoints.ATTRIBUTES.ATTRIBUTE_ENDPOINT
}


export let selfRoutes = {
  list: "/dash/attributes",
  create: "/dash/attributes/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/attributes/" + id + '/update'
  }
}
