import {IStock} from "../../stocks/models/stock.model";
import {Locales} from "../../../../models/global.config";
import {IProductBrief} from "../../products/models/product.model";

export interface IStockHistory {
  id: number
  created_by: string
  created_at: Date
  updated_at: Date | null
  quantity: number
  unit_price: number
  retail_price: number
  discount_percentage: number
  cashback_percentage: number
  discount_valid_until: Date | null
  cashback_valid_until: Date | null
  stock_id: number
  product_name: string
  product_name_bn: string
}

export function resolveProductName(p: IStockHistory, locale: string) {
  if (locale === Locales.BN_BN) {
    return p.product_name_bn == null ? p.product_name : p.product_name_bn
  } else {
    return p.product_name
  }
}

export interface IStockHistoryDetail {
  id: number
  created_by: string
  created_at: Date
  updated_at: Date | null
  quantity: number
  unit_price: number
  retail_price: number
  discount_percentage: number
  cashback_percentage: number
  discount_valid_until: Date | null
  cashback_valid_until: Date | null
  stock_id: number
  stock: IStock
}

export interface IStockHistoryReq {
  title: string
  description: string
  image: string | null
}
