import {Component, Inject, LOCALE_ID} from '@angular/core';
import {defaultStockConfig, selfRoutes, StockConfig} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData, FilterField,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {StockService} from "../../services/stock.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {StockUpsertComponent} from "../stock-upsert/stock-upsert.component";
import {IStock, IStockDetail,} from "../../models/stock.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {BaseConfig, BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {BaseListComponentV3} from "../../../../../../../base/components/base-list-v3.component";
import {ICategoryBrief} from "../../../categories/models/category.model";
import {IVendor} from "../../../vendors/models/vendor.model";
import {VendorService} from "../../../vendors/services/vendor.service";
import {environment} from "../../../../../../../../environments/environment";
import {resolveProductName} from "../../../products/models/product.model";
import {
  StockHistoryListComponent
} from "../../../stockhistories/components/stockhistory-list/stockhistory-list.component";
import {IStockHistory} from "../../../stockhistories/models/stockhistory.model";
import {BaseListComponentForOrg} from "../../../../organizations/components/org-base-list.component";
import {OrganizationService} from "../../../../organizations/services/organization.service";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";

@Component({
  selector: 'app-stock-list',
  templateUrl: './stock-list.component.html',
  styleUrls: ['./stock-list.component.scss']
})
export class StockListComponent extends BaseListComponentForOrg<IStock, IStockDetail> {

  config: StockConfig = defaultStockConfig
  filterData: BasicFilterData = defaultFilterData

  stocks: IPage<IStock> = defaultPage

  extraFields: FilterField[] = [
    {
      name: 'vendor_id',
      type: 'dropdown',
      placeholder: 'Select Store',
      label: 'Store',
      value: null,
      options: []
    }
  ]

  protected componentName(): string {
    return "StockListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService,
              dialog: DialogService<IStockDetail>,
              private activatedRoute: ActivatedRoute,
              private stockService: StockService,
              private vendorService: VendorService,
              private notifier: NotifierService,
              @Inject(LOCALE_ID) protected locale: string,
              private historyDialog: DialogService<IStockHistory>,
              orgService: OrganizationService
  ) {
    super(eventBus, router, subContainer, dialog, orgService)
    if (authService.isAdmin()) {
      this.config.endpoint = InventoryEndpoints.STOCKS.STOCK_ENDPOINT_ADMIN_V2
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      let vendorId = this.filterData.extra['vendor_id']

      this.onOrgLoaded((orgId) => {
        this.fetchMyVendors({org_id: orgId}, (vendors: IVendor[]) => {
          this.extraFields = [
            {
              name: 'vendor_id',
              type: 'dropdown',
              label: 'Vendor',
              placeholder: 'Select Store',
              value: null,
              options: vendors.map(v => {
                return {
                  label: v.name,
                  value: v.id
                }
              })
            }
          ]
        })

        this.fetchStocks({
          vendor_id: vendorId,
          q: this.filterData.query,
          username: this.filterData.username,
          from_date: this.filterData.fromDate,
          to_date: this.filterData.toDate,
          page: this.filterData.page,
          size: this.filterData.size,
          org_id: orgId
        })
      })

    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: this.translations["Stocks"],
        link: null,
        active: true
      }
    ]
  }

  private fetchStocks(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STOCKS.SEARCH(params))
    this.stockService.search(key, this.config.endpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.stocks = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Stock is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfigV2 {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV3<IStockDetail>> | null {
    return StockUpsertComponent
  }

  onCopyClick(sku: string) {
    TextUtility.copyToClipboard(sku)
    this.notifier.info("Copied", "SKU has been copied to clipboard")
  }

  getDiscountedRetailPrice(item: IStock): number {
    return item.retail_price - this.getDiscount(item)
  }

  getDiscount(stock: IStock): number {
    if (
      stock.discount_valid_until == null ||
      new Date(stock.discount_valid_until).getTime() > new Date().getTime()
    ) {
      return stock.retail_price * (stock.discount_percentage / 100)
    } else {
      return 0
    }
  }

  categoriesToString(categories: ICategoryBrief[]): string {
    return categories.map(category => category.name)
      .join(', ')
  }

  private fetchMyVendors(params: IParams = {}, block: (vendors: IVendor[]) => void) {
    let key = this.componentKey(EventKey.VENDORS.MY_VENDORS(params))
    this.vendorService.myVendors(key, this.config.vendorEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      let vendors = meta.data as IVendor[]
      block(vendors)
    })
  }

  private fetchBarcode(stockId: number, params: IParams = {}) {
    let key = this.componentKey(EventKey.STOCKS.GET_BARCODE(stockId))
    this.stockService.fetchBarcode(key, stockId, params)
    this.subscribe(key, null, (value, meta) => {
      let url = meta.data as string
      console.log("BARCODE SAFE URL ", url)
    })
  }

  getBarcodeUrl(stockId: number): string {
    return environment.base_url + "/api/v1/stocks/" + stockId + "/generate-barcode-sku?height=30&width=50&access_token="
      + this.authService.getAccessToken()
  }

  onPrintClick(item: IStock) {
    let printFrame = document.createElement('iframe');
    printFrame.style.display = 'none';
    document.body.appendChild(printFrame);

    // Create container element for text and image
    let printContent = document.createElement('div');
    printContent.style.display = 'flex';
    printContent.style.flexDirection = 'column';
    printContent.style.alignItems = 'center';
    printContent.style.justifyContent = 'center';

    // Create top text element
    let topText = document.createElement('p');
    topText.textContent = resolveProductName(item.product, this.locale); // Replace with your logic
    topText.style.margin = '0'; // Remove default margin
    topText.style.fontSize = '14px'; // Adjust font size

    // Create image element
    let image = document.createElement('img');
    image.src = this.getBarcodeUrl(item.id);

    // Create bottom text element
    let bottomText = document.createElement('p');
    bottomText.textContent = item.sku; // Replace with your logic
    bottomText.style.margin = '0'; // Remove default margin
    bottomText.style.fontSize = '12px'; // Adjust font size

    // Append all elements to content container
    printContent.appendChild(topText);
    printContent.appendChild(image);
    printContent.appendChild(bottomText);

    // Write the content to the iframe
    printFrame.contentDocument!!.write(printContent.outerHTML);
    printFrame.contentDocument!!.close();

    // Wait for content to be fully loaded before printing
    printFrame.onload = function () {
      printFrame.contentWindow!!.print();
      if (!printFrame.contentWindow!!.focus!!) {
        document.body.removeChild(printFrame);
      }
    };
  }


  protected readonly resolveProductName = resolveProductName;

  onHistoryClick(item: IStock) {
    const dialogRef = this.historyDialog.open(StockHistoryListComponent, {
      id: item.id,
      stockId: item.id,
      isWithinDialog: true
    }, false)
    let component = dialogRef.componentInstance
    component.onCanceled.subscribe(() => {
      dialogRef.close()
    })
    component.onCompleted.subscribe(() => {
      this.ngOnInit()
      console.log("onSuccess. closing dialog")
      dialogRef.close()
    })
  }
}
