import {Component, ElementRef, ViewChild} from '@angular/core';
import {IStock} from "../../models/stock.model";
import {environment} from "../../../../../../../../environments/environment";
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {StockSelectComponent} from "../stock-select/stock-select.component";
import {defaultPage} from "../../../../../../../base/models/IPage";

@Component({
  selector: 'app-print-barcode',
  templateUrl: './print-barcode.component.html',
  styleUrls: ['./print-barcode.component.scss']
})
export class PrintBarcodeComponent extends BaseComponent {
  @ViewChild("stockSelect") stockSelect: StockSelectComponent
  @ViewChild('printableContent') printableContent: ElementRef;

  selectedStock: IStock | null = null

  stocks: { sku: string, name: string, barcode: string }[] = []


  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService
  ) {
    super(eventBus, router, subContainer)

  }

  protected componentName(): string {
    return PrintBarcodeComponent.name;
  }

  ngOnInit(): void {
  }


  onStockSelected(stock: IStock | null) {
    if (stock == null) {
      return
    }
    let item = {
      stock: stock,
      quantity: 1,
      availableQuantity: stock.quantity,
      discount: stock.discount_percentage
    }

    this.stocks.push(
      {
        sku: stock.sku,
        name: stock.product.name,
        barcode: this.getBarcodeUrl(stock.id)
      }
    )

    this.stockSelect.clear()
  }

  getBarcodeUrl(stockId: number): string {
    return environment.base_url + "/api/v1/stocks/" + stockId + "/generate-barcode-sku?height=30&width=50&access_token="
      + this.authService.getAccessToken()
  }


  removeItem(index: number) {
    this.stocks.splice(index, 1)
  }

  onPrintClick() {
    // const printContents = this.printableContent.nativeElement.innerHTML;
    // const originalContents = document.body.innerHTML;
    //
    // document.body.innerHTML = printContents;

    window.print();

    // document.body.innerHTML = originalContents;
  }

  onClear() {
    let c = confirm("Are you sure?")
    if (!c) return
    this.stocks = []
  }
}
